import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
* Fetches grants
* @returns {Promise} response - a promise of a grants arrays
*/
export function getGrants(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/entrepreneurshipgrants?limit=${limit}&offset=${offset}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches a grant
* @params Number id - ID of grant
* @returns {Promise} response - a promise of a grants arrays
*/
export function getGrant(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/entrepreneurshipgrants/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Change a grant status
* @params Number id - ID of grant
* @returns {Promise} response - a promise of a grants arrays
*/
export function changeGrantStatus(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/entrepreneurshipgrants/${id}/status`, data);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}
