<template>
	<div>
		<template v-if="loadingGrant">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row">
				<div class="main__primary">
					<h3 class="user-profile__name">{{ itemData.project_name }}</h3>
				</div>
				<div class="user-profile__card">

					<div class="user-profile__section">
						<h5 class="user-profile__heading">Grant Summary</h5>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Project Description:</p>
							<p>{{ itemData.project_description }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Application Reason:</p>
							<p>{{ itemData.application_reason }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Fund Purpose:</p>
							<p>{{ itemData.fund_purpose }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Expected Amount:</p>
							<p>{{ itemData.expected_amount }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Document:</p>
							<span>
								<button class="btn btn__download" type="button">
									<a v-bind:href="itemData.document" target="_blank">Download Grant Document</a>
								</button>
							</span>
						</div>
						
						<div class="user-profile__item">
							<p class="user-profile__section__title">
								Status: 
								<span class="grant-status__text">
									{{ itemData.status | capitalize }}
								</span>
							</p>
							<template v-if="itemData.status == 'new'">
								<button
									@click="changeGrantStatus('accepted')"
									class="btn btn__outline grant-button"
									type="button">
									Accept Grant
								</button>
								<button
									class="btn btn__danger grant-button"
									@click="changeGrantStatus('rejected')"
									type="button">
									Reject Grant
								</button>
							</template>
						</div>	
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import Loader from '../../components/Loader';
import { getGrant, changeGrantStatus } from '../../utils/api/grant';
import eventHub from '../../utils/eventhub';
import { success, fail } from '../../utils/toast';

export default {
	name: 'GrantDetail',
	data() {
		return {
			itemData: {},
			loading: false,
			loadingGrant: true,
			grantLoaded: false,
			status: null
		};
	},
	created() {
		this.getGrant();
		eventHub.$on('grant-status-updated', (payload) => {
			this.itemData.status = payload.status;
		})
	},
	methods: {
		getGrant() {
			getGrant(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.grantLoaded = true;
						this.itemData = response.data.data;
						this.status = itemData.status;
						return true;
					}
					this.loadingGrant = false;
					return false;
				})
				.catch((error) => {
					return error;
				})
				.finally(() => {
					this.loadingGrant = false;
				});
			return true;
		},
		changeGrantStatus(value) {
			const data = { status: value }
			changeGrantStatus(this.$route.params.id, data)
				.then((response) => {
					if (response.status === 200) {
						eventHub.$emit('grant-status-updated', response.data);
						success({
							title: `Success! Grant has been ${data.status}!`
						});
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	components: {
		Loader
	},
	filters: {
		capitalize(value) {
			if (!value) return ''
			value = value.toString()
			return value.charAt(0).toUpperCase() + value.slice(1)
		}
	}

};
</script>

